import './App.css';
import React, { useState, useRef, useEffect } from 'react';
import womenData from './womenData.js';
import menData from './menData.js';
import Detail from './detail.js';
import Detail2 from './detail2.js';
import Cart from './Cart.js';
import { Container, Row, Nav, Navbar } from 'react-bootstrap';
import { Link, Route, Switch, useHistory } from 'react-router-dom';
import { TweenMax, Power3} from 'gsap';

function App() {
  let app = useRef(null);
  let imgMove = useRef(null);
  let text1 = useRef(null);
  let text2 = useRef(null);
  let text3 = useRef(null);

  const [imgSize, setImgSize] = useState(false);
  
  const handleExpand = () => {
    TweenMax.to(imgMove, .8, { width: 600, height: 600, ease: Power3.easeOut })
    setImgSize(true)
  }
  const handleShrink = () => {
    TweenMax.to(imgMove, .8, { width: 500, height: 500, ease: Power3.easeOut })
    setImgSize(false)
  }
  useEffect(() => {
    TweenMax.to(app, 0, { css: { visibility: 'visible' } })
    TweenMax.staggerFrom([imgMove, text1, text2, text3], .8, { opacity: 0, x: 40, ease: Power3.easeOut }, .2)
    
  }, []);

  let [data, setdata] = useState(womenData);
  let [dataMen, setdataMen] = useState(menData);
  let history = useHistory();

  let [cartItems, setCartItems] = useState([]);


  
  return (
    
    <div className="App" ref={el => app = el}> 
      
      <Route exact path="/">
     
       <Navmenu></Navmenu>
        <Container className="mainPage">
          <Row>
            <div className="mainImg col-md-6"
            ref={el => imgMove = el}
          onClick={imgSize !== true ? handleExpand : handleShrink}>
            <img src="./imgs/w2.jpg" alt="mainimg"/>
        </div>
        <div className="mainText col-md-6">
          <h1 className="mainText-1" ref={el => text1 = el}>THE GOOD</h1>
          <h1 className="mainText-2" ref={el => text2 = el}>SPRING</h1>
            <h1 className="mainText-3" ref={el => text3 = el}>LOOKBOOK</h1>
            <button>SHOP NOW</button>
        </div> 

          </Row>
           
        </Container>
        <Container>
         
      </Container>
    </Route>
      


      <Route path="/women">
        <Navmenu></Navmenu>
        <div className="main">
        <div>
      {
        womenData.map((a,i) => {
          return <Card data={data[i]} i={i} key={i}></Card>
        })
      }
        </div>
      </div>
      

      </Route>
      <Route path="/men">
        <Navmenu></Navmenu>
        <div className="main">
        <div>
      {
        menData.map((a,i) => {
          return <Card2 dataMen={dataMen[i]} i={i} key={i}></Card2>
        })
      }
        </div>
      </div>
      
      </Route>
      <Route path="/all">
        <Navmenu></Navmenu>
        <div className="main">
        <div>
      {
        womenData.map((a,i) => {
          return <Card3 data={data[i]} i={i} key={i}></Card3>
        })
      }
        </div>
      </div>

      </Route>

      <Route path="/detail/:id"> 
        <Navmenu></Navmenu>
        <Detail data={data}/>
      </Route>
      <Route path="/detail2/:id"> 
        <Navmenu></Navmenu>
        <Detail2 dataMen={dataMen}/>
      </Route>
      <Route path="/cart"> 
        <Navmenu></Navmenu>
        <Cart />
        </Route>
      
    
      
    </div>
  );
}

function Navmenu() {
  return (
    

   <Navbar variant="light">
    <Navbar.Brand as={Link} to="/">THEGOOD</Navbar.Brand>
    <Nav className="mr-auto">
      <Nav.Link as={Link} to="/women">WOMEN</Nav.Link>
      <Nav.Link as={Link} to="/men">MEN</Nav.Link>
      <Nav.Link as={Link} to="/all">ALL</Nav.Link>
    </Nav>
    <Nav className="mr-right gnb">
      <Nav.Link as={Link} to="/cart"> <img src="./imgs/cart.svg" alt=""/> <span>0</span> </Nav.Link>
      <Nav.Link href="#features" ><img src="./imgs/user.svg" alt=""/></Nav.Link>
      <Nav.Link href="#pricing"><img src="./imgs/search.svg" alt=""/></Nav.Link>
    </Nav>
    </Navbar>
    )
  
}

function Card(props) {
  let history = useHistory();
  return (
    <div className="womenCard" onClick={()=>{history.push('/detail/'+ props.data.id)}} >
      <img src={'/imgs/w'+(props.i+1)+'.jpg'}/>
      <div className="textBox">  
        <h4>{ props.data.title}</h4>
        <p>{props.data.price}</p>
      </div>  
    </div>
  )
}
function Card2(props) {
  let history = useHistory();
  return (
    <div className="womenCard" onClick={()=>{history.push('/detail2/'+ props.dataMen.id)}}>
      <img src={'/imgs/m'+(props.i+1)+'.jpg'}/>
      <div className="textBox">  
        <h4>{ props.dataMen.title}</h4>
        <p>{props.dataMen.price}</p>
      </div>  
    </div>
  )
}
function Card3(props) {
  return (
    <div className="womenCard" >
      <img src="/imgs/w1.jpg"/>
      <div className="textBox">  
        <h4>{ props.data.title}</h4>
        <p>{props.data.price}</p>
      </div>  
    </div>
  )
}
export default App;
