import React, { useState } from 'react';
import './cart.css';
import { useHistory, useParams } from 'react-router-dom';
import { Container, Table } from 'react-bootstrap';
import {connect} from 'react-redux';

function Cart(props) {
    let [btn, activeBtn] = useState(true);
    const itemPrice = props.state.reduce((a, c) => a + c.price * c.quan, 0);
    const shippingPrice = itemPrice > 500000 ? 0 : 3000;
    const total = itemPrice + shippingPrice;
    
    
    
    return (
        <Container className="cartWrap">
          
            <div className="cartLeft">
                {props.state.length !== 0 && ( <p>장바구니</p>)} 
               
                
        <Table responsive>
            {props.state.length===0 && <div className="cartEmpty">Cart is empty</div>}
        {   
            props.state.map((a,i)=>{
                return (
           <tr>
          <td className="tdImg"><img src={'/imgs/w'+(a.img+1)+'.jpg'} alt=""/></td>
          <td>{a.title}</td>
          <td>size {a.size}</td>
          <td>
              <span className="minus" onClick={()=>{props.dispatch({type:'decrease', data: a.id})}}>-</span>
              <span>{a.quan}</span> 
              <span className="plus" onClick={()=>{props.dispatch({type : 'increase', data: a.id})}}>+</span>
            </td>
          <td>{a.price}</td>
                        <td><button className="closebtn" onClick={() => { props.dispatch({ type: 'remove', data: a.id }) }}>
                            x
                            </button>
                        </td>
          

        </tr>
                )
                
            })
        }
                </Table>
                
                {props.state.length !== 0 && (
                    <div className="alert">
            <p>지금 구매하시면 신규할인 20%</p>
            <button className="popupClose">close</button>
        </div>
                    
        )}        
      
            </div>
            {
                props.state.length !== 0 &&
                ( <div className="cartRight">
                <p>주문요약</p>
                <Table>
                    <tr>
                        <td className="priceTitle"> Item Price</td>
                        <td className="priceCart">${ itemPrice.toFixed(2)}</td>
                    </tr>
                    <tr>
                        <td className="ship">shipping</td>
                        <td className="shipFee">${ shippingPrice.toFixed(2)}</td>
                    </tr>

                </Table>
                <p className="total">total ${ total.toFixed(2)}</p>

            </div>)
                
            }

           
            
        </Container>
    )
}

function stateToprops(state) {
    return {
        state: state
    }
}
export default connect(stateToprops)(Cart);