import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import '../node_modules/bootstrap/dist/css/bootstrap.css';
import { BrowserRouter } from 'react-router-dom';
import {Provider} from 'react-redux';
import { createStore } from 'redux';
import { MuiThemeProvider, creatMuitheme, createMuiTheme } from '@material-ui/core/styles';

const theme = createMuiTheme({
   typography: {
    fontFamily: 'moument',
  },
  typography: {
    fontFamily :'moumentRegular',
  },
});

let initialValue = ([]);
function reducer(state = initialValue, action) {
  if (action.type === 'add') {
    let found = state.findIndex((a) => { return a.id === action.data.id });
    if (found>=0) {
      let copy = [...state];
      copy[found].quan++;
      return copy
    } else {
      let copy = [...state];
    copy.push(action.data);
      return copy
      
    }
    
  } else if (action.type === 'remove') {
    let found = state.findIndex((a) => { return a.id === action.data });
    let copy = [...state];
    if (found) {
      delete copy[found];
      return copy
    }
    return copy
      
  }
  else if (action.type === 'increase') {
    let copy = [...state];
    copy[action.data].quan++;
    return copy
  } else if (action.type === 'decrease') {
    let copy = [...state];
    if (copy[action.data].quan > 0) {
      copy[action.data].quan--;
    }
    return copy
  } else {
    return state
  }
  

}
let store = createStore(reducer);

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter >
      <Provider store={store}>
        <MuiThemeProvider theme={theme}>
       <App />
        </MuiThemeProvider>
     
     </Provider>
    </BrowserRouter>

  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
